import React, { Component } from 'react';
import './App.css';

class Ingredient extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: this.props.name,
            image: this.props.image,
            x: this.props.x,
            y: this.props.y,
            collected: false
        };
    }

    render() {
        return (
            <div className="ingredient" style={{left:this.props.x, top:this.props.y, display:'block'}}>
                <button className="ingredient">
                    <img src={this.props.image} />
                </button>
            </div>
        )
    }
}

export default Ingredient